/* src/app/welcome/welcome.scss */
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}
body {
  font-family:
    "Inter",
    Arial,
    sans-serif;
  background: #f7f6f4;
  color: #212121;
  margin: 0;
  padding: 0;
}
:host {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 80vh;
  font-family:
    "Inter",
    Arial,
    sans-serif;
  background: #f7f6f4;
}
.ds-welcome-hero {
  display: flex;
  flex-direction: row;
  align-items: stretch;
  background:
    linear-gradient(
      90deg,
      #ffffff 70%,
      #ff6200 120%);
  border-radius: 32px;
  box-shadow: 0 8px 32px rgba(33, 33, 33, 0.09), 0 2px 6px rgba(255, 98, 0, 0.1);
  width: 100%;
  max-width: 960px;
  min-height: 340px;
  overflow: hidden;
}
.ds-welcome-hero__left {
  flex: 1 1 0;
  padding: 32px 32px 32px 32px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-width: 320px;
}
.ds-welcome-logo {
  margin-bottom: 24px;
  filter: drop-shadow(0 3px 12px rgba(255, 98, 0, 0.21));
}
.ds-welcome-slogan {
  color: #ff6200;
  font-size: 32px;
  font-weight: 700;
  margin-bottom: 12px;
  line-height: 1.1;
  letter-spacing: 0.5px;
  font-family:
    "Inter",
    Arial,
    sans-serif;
  text-shadow: 0 1px 10px rgba(255, 98, 0, 0.07);
}
.ds-welcome-desc {
  color: #212121;
  font-size: 24px;
  font-weight: 400;
  font-family:
    "Inter",
    Arial,
    sans-serif;
  margin-bottom: 0;
  line-height: 1.5;
  text-shadow: 0 1px 6px rgba(236, 236, 236, 0.11);
}
.ds-welcome-hero__right {
  display: flex;
  align-items: center;
  justify-content: center;
  background: #ff6200;
  flex: 0 0 320px;
  min-width: 220px;
  position: relative;
}
.ds-welcome-feito {
  color: #ffffff;
  font-size: 5.5rem;
  font-weight: 700;
  font-family:
    "Inter",
    Arial,
    sans-serif;
  letter-spacing: 3px;
  text-shadow: 0 6px 32px rgba(33, 33, 33, 0.2), 0 2px 8px rgba(255, 255, 255, 0.13);
  margin-right: -24px;
  margin-left: 24px;
  -webkit-user-select: none;
  user-select: none;
}
@media (max-width: 900px) {
  .ds-welcome-hero {
    flex-direction: column;
    min-height: 0;
    max-width: 98vw;
  }
  .ds-welcome-hero__right {
    min-width: 0;
    flex: none;
    width: 100%;
    justify-content: flex-end;
    padding: 24px;
    background:
      linear-gradient(
        90deg,
        #ff6200 70%,
        #ffffff 100%);
  }
  .ds-welcome-feito {
    font-size: 3.5rem;
    margin-right: 0;
    margin-left: 0;
  }
  .ds-welcome-hero__left {
    padding: 32px 24px 24px 32px;
    min-width: 0;
  }
}
@media (max-width: 500px) {
  .ds-welcome-hero__left {
    padding: 24px;
  }
  .ds-welcome-feito {
    font-size: 2.2rem;
  }
}
.ds-welcome-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  background: #ececec;
  border-radius: 32px;
  width: 180px;
  height: 180px;
  margin: 40px auto;
}
.ds-welcome-smile {
  font-family:
    "Inter",
    Arial,
    sans-serif;
  font-size: 80px;
  font-weight: 700;
  color: #ff6200;
  margin: 0;
  line-height: 1;
  -webkit-user-select: none;
  user-select: none;
}
/*# sourceMappingURL=welcome.css.map */
